//-----key-visual
.kv-ul-wrapper {
  display: flex;
  justify-content: center;
  background-image: url(/asset/plan@2x.png);
  background-size: cover;
  max-width: 100%;
  height: auto;
  padding-top: 115px;
  padding-bottom: 114px;

  .kv-ul-content {

    .kv-ul-copy {
      font-size: 3.2rem;
      font-weight: bold;
      color: #FFF;
    }
  }
}
//-----key-visual//@at-root

//-----section-ul-price
#section-ul-price{
  margin-top: 60px;

  .section-ttl{
    text-align: center;
  }

  .section-inner-ul-price{
    display: flex;
    justify-content: center;
    margin-top: 60px;

    .price-const{
      width: 280px;
      height: 90px;
      background-color: $main-color;
      color: #fff;
      font-size: 2.2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bold;
    }

    .plus-label{
      width: 30px;
      margin-right: 35px;
      margin-left: 35px;
      position: relative;

      &::before,&::after{
        position: absolute;
        top: 50%;
        display: block;
        content:"";
        width: 25px;
        height: 3px;
        background-color: $main-color;
      }

      &::after{
        transform: rotate(90deg);
      }
    }
  }

  .ul-price-sub{
    margin-top: 45px;
    font-size: 1.6rem;
    line-height: 1.6;
    color: $main-color;
  }
}
//-----section-ul-price//@at-root

//-----section-ul-pricelist
#section-ul-pricelist{

  .section-ttl{
    margin-top: 90px;
    text-align: center;
  }

  .section-inner-ul-pricelist{
    margin-top: 106px;
    display: flex;
    justify-content: space-between;

    .price-list-content{
      width: 220px;
      height: 470px;
      border:1px solid $main-color;
      cursor: pointer;

      .price-list-ttl{
        height: 60px;
        font-size: 1.6rem;
        background-color: $main-color;
        color: #fff;
        font-weight: bold;
        display: flex;
        align-items: center;
        justify-content: center;

        &:hover{
          height: 80px;
          background-color: $btn-color;
          transition: 1s;
          transform: translateY(-20px);
        } 

        &:hover + .price-list-inner{
          margin-top: 10px;
          transition: 1s;
        }

        &:hover + .price-list-inner .price{
          color: $btn-color;
          transition: 1s;
        }
      }

      .price-list-inner{
        margin-top: 30px;
        
        .price{
          font-size: 2.2rem;
          font-weight: bold;
          color: $main-color;
          text-align: center;
        }

        .price-sub{
          margin-top: 14px;
          margin-bottom: 48px;
          font-size: 1.6rem;
          color: $main-color;
          text-align: center;
        }

        p{
          text-align: center;
          font-size: 1.6rem;
          color: $main-color;
        }

        ul{

          li{
            width: 146px;
            margin-bottom: 23px;
            margin-right: auto;
            margin-left: auto;
            font-size: 1.6rem;
            color: $main-color;
            position: relative;
            text-align: center;


            &::before,&::after{
              position: absolute;
              display: block;
              content:"";
              background-color: $main-color;
            }

            &::before{
              top: 30%;
              left: -8%;
              width: 3px;
              height: 8px;
              transform: rotate(-45deg);
            }

            &::after{
              top: 20%;
              left: -5%;
              width: 3px;
              height: 10px;
              transform: rotate(45deg);
            }
          }

          li:nth-child(4){

            &::before{
              top: 25%;
            }
          }
        }
      }
    }
  }
}